.uline {
  position: relative;
  white-space: nowrap;
}

.uline:before {
  content: "";
  position: absolute;
  bottom: -0.2em;
  right: 0;
  left: 0;
  height: 0.05em;
  min-height: 1px;
  background-color: currentColor;
  transition: transform 0.55s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transform-origin: right;
  transform: scaleX(0);
}

.uline.is-active:before,
.uline:hover:before {
  transform-origin: left;
  transform: scaleX(1);
}

.uline-double {
  display: inline-flex;
  position: relative;
  white-space: nowrap;
}

.uline-double:after,
.uline-double:before {
  content: "";
  position: absolute;
  bottom: -0.2em;
  right: 0;
  left: 0;
  height: 0.05em;
  min-height: 1px;
  background-color: currentColor;
}

.uline-double:before {
  transform-origin: left;
  transform: scaleX(1);
  transition: transform 0.55s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition-delay: 0.5s;
}

.uline-double:after,
.uline-double:hover:before {
  transform-origin: right;
  transform: scaleX(0);
  transition: transform 0.55s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition-delay: 0s;
}

.uline-double:hover:after {
  transform-origin: left;
  transform: scaleX(1);
  transition: transform 0.55s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition-delay: 0.2s;
}

@media (max-width: 649px) {
  .uline-double.--no-s:after,
  .uline-double.--no-s:before {
    content: none;
  }
}

.touch-scroll {
